export default{
	props:{
		hotGood:{
			type:Object
		}
	},
	onLoad() {
		//console.log(JSON.stringify(this.hotGood));
	},
	computed:{
		jhj(){
			return this.hotGood.zk_final_price-this.hotGood.youhuiquan;
		},
		yj() {
			var v=this.hotGood.youhuiquan * (parseFloat(this.hotGood.commission_rate / 100))/100
			return v.toFixed(2);
		}
	}
}
